.oms-dogear {
  position: relative;
  outline: 0;
}
.oms-dogear::after {
  box-shadow: inset 0 0 0 4px #0078aa;
  content: "";
  opacity: 0;
  position: absolute;
  top: -1px;
  right: -1px;
  bottom: -1px;
  left: -1px;
  transition: opacity 0.1s linear;
  z-index: 2;
}
.oms-dogear:focus::after, .oms-dogear:hover::after {
  opacity: 1;
}
.oms-dogear:active::after {
  box-shadow: inset 0 0 0 4px #005b81;
  opacity: 1;
}
.oms-dogear .oms-dogear-indicator {
  position: absolute;
  top: -1px;
  right: -1px;
  border: 1.5rem solid transparent;
  border-right-color: #e8e8e8;
  border-top-color: #e8e8e8;
}
.oms-dogear .oms-dogear-indicator::before {
  font-family: "mylt-icons";
  content: "";
  color: #707070;
  position: absolute;
  top: -1.25rem;
  right: -1.25rem;
  z-index: 2;
}

.oms-dogear-active {
  border-color: #0078aa;
}
.oms-dogear-active::before {
  box-shadow: inset 0 0 0 2px #0078aa;
  content: "";
  position: absolute;
  top: -1px;
  right: -1px;
  bottom: -1px;
  left: -1px;
  z-index: 2;
}
.oms-dogear-active .oms-dogear-indicator {
  border-right-color: #0078aa;
  border-top-color: #0078aa;
}
.oms-dogear-active .oms-dogear-indicator::before {
  content: "";
  color: #fff;
}

